import React from "react"
import { useEffect, useState } from 'react';
import {Link} from "gatsby"
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';

import SEO from "../components/seo"

import logo from "../../static/images/logo.webp"
import iconTwitter from "../../static/images/icon-twitter.svg"
import iconDiscord from "../../static/images/discord.svg"
import featureImage from "../../static/images/edm.gif";
import "../../static/styles/main.scss"

// Contract info
import contract from '../contracts/AlphaShark.json';
import { ethers } from 'ethers';

const contractAddress = "0x2ACb22B7A45D1f11Ab18ab5690d73e6e261F61b4";
const abi = contract.abi;

const IndexPage = () => {
    const [currentAccount, setCurrentAccount] = useState(null);
    const [mintNumber, setmintNumber] = useState(0);
    const [mintLog, setMintLog] = useState(null);
    const [mintWhiteLog, setMintWhiteLog] = useState(null);

    const checkWalletIsConnected = async () => {
        const { ethereum } = window;
    
        if (!ethereum) {
          console.log("请确保已经安装MetaMask!");
          return;
        } else {
          console.log("Wallet exists! We're ready to go!")
        }
    
        const accounts = await ethereum.request({ method: 'eth_accounts' });
    
        if (accounts.length !== 0) {
          const account = accounts[0];
          console.log("Found an authorized account: ", account);
          setCurrentAccount(account);
        } else {
          console.log("No authorized account found");
        }
      }
    
    const connectWalletHandler = async () => {
        const { ethereum } = window;

        if (!ethereum) {
            alert("请安装Metamask 狐狸钱包");
        }

        try {
            const accounts = await ethereum.request({ method: 'eth_requestAccounts' });
            console.log("Found an account! Address: ", accounts[0]);
            setCurrentAccount(accounts[0]);
        } catch (err) {
            console.log(err)
        }
    }

    const mintNftHandler = async () => {
        try {
          const { ethereum } = window;
    
          if (ethereum) {
            const provider = new ethers.providers.Web3Provider(ethereum);
            const signer = provider.getSigner();
            const nftContract = new ethers.Contract(contractAddress, abi, signer);
    
            setMintLog('开始处理');
            let nftTxn;
            try {
                nftTxn = await nftContract.mintWinnerShark(mintNumber, { value: ethers.utils.parseEther(0.45*mintNumber+"") });
                const txInfo = `请查看交易`
                setMintLog(txInfo);
            } catch (e) {
                console.log(e.error.message)
                setMintLog(e.error.message+"");
            }
          } else {
            setMintLog("请连接钱包");
          }
        } catch (err) {
          console.log(err);
        }
      }

    const handleChange = (event) => {
        setmintNumber(event.target.value);
      };

    useEffect(() => {
        checkWalletIsConnected();
      }, [])


    return(
        <body>
            <header>
                <div className={"container"}>
                    <div className={"top-menu"}>
                        <div className={"logo"}>
                            <Link to="/" title={"Shark Club"}>
                                <img alt={"Logo"} src={logo}/>
                            </Link>
                        </div>
                        <div className={"link"}>
                            <Link to="/" title={"The Ancients 古人DAO"}>回到官網</Link>
                        </div>
                        <div className={"get-started"}>
                            <button 
                                onClick={connectWalletHandler} 
                                className='cta-button connect-wallet-button'>
                                {currentAccount? currentAccount.slice(0, 4)+"..."+currentAccount.slice(38)  :"连接钱包"}
                            </button>
                        </div>
                    </div>
                </div>
            </header>
            <SEO title="Public Mint"/>
            <main>
            <img className={"feature"} alt={"Dashboard"} src={featureImage}/>
            <div className={"page-header home"}>
                <h1>一起加入古人DAO</h1>
                <h2>Public Mint</h2>
                <p>限量2013只/每人上限2只</p>
                <p>每只0ETH</p>
                <Box
                    component="form"
                    sx={{
                        '& > :not(style)': { m: 1, width: '25ch' },
                    }}
                    noValidate
                    autoComplete="off"
                    >
                    <TextField
                        color="secondary"
                        focused
                        id="outlined-name"
                        label="输入数量"
                        value={mintNumber}
                        inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' , id: "input"}}
                        onChange={handleChange}
                    />
                </Box>
            
                    <button 
                        onClick={mintNftHandler} 
                        className='cta-button mint-nft-button'>
                         Mint NFT
                    </button>
             
                <p>{mintLog}</p>
                <br />
                
            </div>
            
            </main>
            <></>
            <footer>
                <div className={"container"}>
                    <div className={"row"}>
                        <div className={"col-5"}>
                            <div className={"widget__item"}>
                                <div className={"logo"}>
                                    <Link to="/" title={"SharkClub"}>
                                        <img alt={"Logo"} src={logo}/>
                                    </Link>
                                </div>

                                <div className={"about"}>
                                    <p>玩轉古今衝擊你對NFT的想像! 全球唯一錢母概念NFT!</p>
                                </div>
                            </div>
                        </div>
                        <div className={"col-4"} />

                        <div className={"col-3"}>
                            <div className={"widget__item"}>
                                <div className={"social"}>
                                    <a href="https://twitter.com/WinnerSharkClub" target={"_blank"} title={"Twitter"}><img alt={"Twitter"} src={iconTwitter}/></a>
                                    <a href="https://discord.gg/winnersharkclub" target={"_blank"} title={"Discord"}><img alt={"Discord"} src={iconDiscord}/></a>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div className={"copyright"}>
                        <p>Copyright {new Date().getFullYear()}, {` `} 古人DAO. All rights reserved.</p>
                    </div>
                </div>
            </footer>

        </body>
    );
}

export default IndexPage
